body {
  background-color: rgba(80, 80, 80, .827);
}

.cat-info {
  max-height: 250px;
  padding: 20px 0;
}

.cat-conteiner {
  border: 1.5px solid #fff;
  border-radius: 10px;
  gap: 30px;
  padding: 10px;
  display: flex;
}

.cat-top-block {
  padding: 10px;
}

.cat-img {
  height: 100%;
}

.cat-title {
  color: #43c0c0;
  margin: 16px 0;
  font-size: 22px;
}

.cat-text {
  margin-top: 0;
  font-size: 12px;
}

.cat-temp {
  font-size: 14px;
}

.pink-text {
  color: #28ca8c;
  font-size: 16px;
}

.loader {
  width: 28px;
  height: 28px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  margin: 0 auto;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.loader, .error, .is-hidden {
  display: none;
}

